<template>
    <swiper :slidesPerView="1" :lazy="true" :navigation="true" :style="{'--swiper-navigation-color': '#fff','--swiper-pagination-color': '#fff'}" :pagination="{
  'clickable': true
    }" :breakpoints="{
      '1200': {
        'slidesPerView': 1,
      }
    }" class="mySwiper h-100"
    >

      <swiper-slide v-for="slide in this.images[this.lang]" :key="slide.section">
        <img :src="slide.src" class="swiper-lazy vh-100"  alt=""/>
        <div class="swiper-lazy-preloader swiper-lazy-preloader-white"></div>
      </swiper-slide>

    </swiper>
</template>
<script>
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/swiper.scss';

import "swiper/components/lazy/lazy.min.css"
import "swiper/components/pagination/pagination.min.css"
import "swiper/components/navigation/navigation.min.css"

// import Swiper core and required modules
import SwiperCore, {
  Lazy,Pagination,Navigation
} from 'swiper/core';

// install Swiper modules
SwiperCore.use([Lazy,Pagination,Navigation]);

export default {
  props: ['images','lang'],
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {

  },
  methods: {}

